<template>
  <el-dialog title="系统更新信息"
             :visible="pageInfo.isShow"
             :modal-append-to-body="false"
             :close-on-click-modal="false"
             :center="true"
             :lock-scroll="false"
             @close="onClose(module_name,'pageDataRef')"
  >
    <el-form :model="pageData" :rules="pageDataRules" ref="pageDataRef" :inline="true" label-width="160px" size="small">
      <el-form-item label="更新版本" prop="version_number">
        <el-input v-model="pageData.version_number"></el-input>
      </el-form-item>

      <el-form-item label="更新时间" prop="version_update_time">
        <el-date-picker
          v-model="pageData.version_update_time"
          type="datetime"
          placeholder="选择日期时间"
          value-format="yyyy-MM-dd HH:mm:ss"
        >
        </el-date-picker>
      </el-form-item>

      <el-form-item label="更新内容" prop="version_content">
        <el-row v-if="pageInfo.pageModel === 'add'" :key=index v-for="(item,index) in pageData.version_content">
          <el-col :span="1" class="contentIndex">{{ index + 1 }}.</el-col>
          <el-col :span="14">
            <el-input type="textarea" style="width:696px" :autosize="{ minRows: 1, maxRows: 5}" v-model="pageData.version_content[index]"></el-input>
          </el-col>
        </el-row>
        <el-row v-if="pageInfo.pageModel==='edit' || pageInfo.pageModel==='view'" :key=index v-for="(item,index) in pageData.version_content">
          <el-col :span="1" class="contentIndex" style=" ">{{ index + 1}}.</el-col>
          <el-col :span="14">
            <el-input type="textarea" style="width:696px" :autosize="{ minRows: 1, maxRows: 5}" v-model="pageData.version_content[index]"></el-input>
          </el-col>
        </el-row>

        <el-row :gutter="10" v-if="pageInfo.pageModel==='add' || pageInfo.pageModel==='edit'">
          <el-col :span="3" :offset="7">
            <el-tooltip content="添加更新内容条目" placement="top">
              <el-button type="primary" @click="addContent"><i class="el-icon-circle-plus-outline"></i> 新增</el-button>
            </el-tooltip>
          </el-col>
          <el-col :span="3" :offset="1">
            <el-tooltip content="删除更新内容条目" placement="top">
              <el-button type="primary" @click="removeContent"><i class="el-icon-remove-outline"></i> 移除</el-button>
            </el-tooltip>
          </el-col>
        </el-row>
      </el-form-item>

      <el-form-item label="备注" prop="remark">
        <el-input type="textarea" v-model="pageData.remark" style="width:696px"></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="onClose(module_name,'pageDataRef')">关 闭</el-button>
      <el-button v-if="pageInfo.pageModel === 'add'" type="primary" @click="onSubmit">确 定</el-button>
      <el-button v-else-if="pageInfo.pageModel === 'edit'" type="primary" @click="onUpdate">更 新</el-button>
    </div>
  </el-dialog>
</template>
<script>

  export default {
    name: 'sysUpdateManageInfo',
    data() {
      return {
        module_name: 'sysUpdateManage',
        refresh_page_method: 'refresh_' + this.module_name,
        pageData: {
          version_content: ['']
        },
        pageDataRules: {
          version_number: [{required: true, message: '请输入更新版本号', trigger: 'blur'}],
          version_update_time: [{required: true, message: '请输入选择日期', trigger: 'blur'}]
        }
      }
    },
    computed: {
      pageInfo() {
        let x_data = this.$setPageInfo(this.module_name);
        if (x_data.pageModel === 'add') {
          this.pageData = {version_content: ['']};
        } else if (x_data.pageModel === 'edit' && (x_data.pageData === null || x_data.pageData['version_content'].length === 0)) {
          this.pageData = {version_content: ['']};
          this.pageData = x_data.pageData
        }
        return x_data;
      }
    },
    methods: {
      addContent() {//新增更新内容输入框
        this.pageData.version_content.push('')
      },
      removeContent() {//移除更新内容输入框
        if (this.pageData.version_content.length >= 2) {
          this.pageData.version_content.pop();
        }
      },
      onSubmit() // 提交按钮
      {
        let _this = this;
        this.$refs.pageDataRef.validate(async (valid) => {
          if (valid) {
            let item = _this.pageData;
            let postRecords = []
            this.pageData.version_content.forEach(r => { // 去除空白条目
              if (r) {
                postRecords.push(r)
              }
            })
            if (postRecords.length == 0) {
              alert('更新内容不能为空')
              return
            }
            item.version_content = postRecords;
            let res = await _this.$insert(item, 'OAMP_insert_sys_update_info');
            if (res !== null) {
              _this.$message.success('新增信息成功');
              _this.$onClose(_this.module_name, 'pageDataRef', _this.refresh_page_method);
            } else {
              _this.$message.error('新增信息失败');
            }
          }
        });
      },
      async onUpdate() // 更新按钮
      {
        let _this = this;
        this.$refs.pageDataRef.validate(async (valid) => {
          if (valid) {
            let item = _this.pageData;
            let postRecords = []
            this.pageData.version_content.forEach(r => { // 去除空白条目
              if (r) {
                postRecords.push(r)
              }
            })
            if (postRecords.length == 0) {
              alert('更新内容不能为空')
              return
            }
            item.version_content = postRecords;
            let res = await _this.$update(item, 'OAMP_update_sys_update_info');
            if (res != null) {
              _this.$message.success('修改信息成功');
              _this.$onClose(_this.module_name, 'pageDataRef', _this.refresh_page_method);
            } else {
              _this.$message.error('修改信息失败');
            }
          }
        });
      },
    }
  }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .el-input {
    width: 260px;
  }

  .el-select {
    width: 260px;
  }

  .el-date-editor.el-input, .el-date-editor.el-input__inner {
    width: 260px;
  }

  .contentIndex {
    position: absolute;
    top: 0.5px;
    left: 3px
  }

</style>



