<template>
  <div style="padding-top: 2px;">
    <el-form :inline="true" ref="searchFormRef" :model="searchFormData" size="mini">
      <el-form-item label="更新版本号" prop="version_number">
        <el-input v-model="searchFormData.version_number" placeholder="请输入更新版本号"></el-input>
      </el-form-item>
      <el-form-item label="更新内容" prop="version_content">
        <el-input v-model="searchFormData.version_content" placeholder="请输入更新内容"></el-input>
      </el-form-item>
      <el-form-item label="更新时间" prop="version_update_time">
        <!--<el-date-picker-->
        <!--v-model="searchFormData.version_update_time"-->
        <!--type="daterange"-->
        <!--range-separator="至"-->
        <!--start-placeholder="开始日期"-->
        <!--end-placeholder="结束日期">-->
        <!--</el-date-picker>-->

        <el-date-picker
          v-model="searchFormData.version_update_time"
          type="datetimerange"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          value-format="yyyy-MM-dd HH:mm"
          :default-time="['00:00:00', '23:59:59']"
        >

        </el-date-picker>
      </el-form-item>
      <div class="v-search-opt">
        <div class="v-pagination">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="searchFormData.pageNum"
            :page-size="searchFormData.pageSize"
            :total="searchFormData.pageCount"
            :page-sizes="[14, 20, 50, 100]"
            layout="total, sizes, prev, pager, next, jumper"
          >
          </el-pagination>
        </div>
        <div class="v-inline">
          <el-button size="mini" type="primary" @click="onSearch"><i class="iconfont icon-chaxun"></i> 查询</el-button>
          <el-button size="mini" type="primary" @click="onReset"><i class="iconfont icon-chongzhi"></i> 重置</el-button>
          <el-button size="mini" type="primary" @click="onAdd(module_name)"><i class="iconfont icon-xinzeng"></i> 新增</el-button>
        </div>
      </div>
    </el-form>
    <hr>
    <el-table :data="tableData" :border="true" :stripe="true" size="mini" :height="getTableHeight(130)">
      <el-table-column fixed type="index" :index="onIndexMethod(searchFormData.pageNum,searchFormData.pageSize)" label="序号" width="50"></el-table-column>
      <el-table-column
        prop="version_number"
        label="更新版本号"
        width="85">
      </el-table-column>

      <el-table-column
        prop="version_update_time"
        label="更新时间"
        width="135">
      </el-table-column>
      <el-table-column
        prop="creator"
        label="创建人"
        width="70">
      </el-table-column>
      <el-table-column
        prop="createTime"
        label="创建时间"
        width="156">
      </el-table-column>

      <el-table-column
        :show-overflow-tooltip="true"
        property="version_content_format"
        label="更新内容">
      </el-table-column>

      <el-table-column
        :show-overflow-tooltip="true"
        property="remark"
        width="180"
        label="备注">
      </el-table-column>

      <el-table-column fixed="right" label="操作" width="160">
        <template slot-scope="scope">
          <el-col :span="8">
            <el-tooltip class="item" effect="dark" content="查看" placement="left">
              <el-button @click.native.prevent="onView(module_name,scope.$index, tableData)" type="text" size="small"><i class="iconfont icon-chakan"></i></el-button>
            </el-tooltip>
          </el-col>
          <el-col :span="8">
            <el-tooltip class="item" effect="dark" content="修改" placement="left">
              <el-button @click.native.prevent="onEdit(module_name,scope.$index, tableData)" type="text" size="small"><i class="iconfont icon-xiugai"></i></el-button>
            </el-tooltip>
          </el-col>
          <el-col :span="8">
            <el-tooltip class="item" effect="dark" content="删除" placement="left">
              <el-button @click.native.prevent="onDelete(scope.$index, tableData)" type="text" size="small"><i class="iconfont icon-shanchu icon-red"></i></el-button>
            </el-tooltip>
          </el-col>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  export default {
    name: 'sysUpdateManageList',
    data() {
      return {
        module_name: 'sysUpdateManage',
        refresh_page_method: 'refresh_' + this.module_name,
        searchFormData: {
          pageNum: 1,
          pageSize: 14,
          pageCount: 1,
          order: '+createTime'
        },
        tableData: [],
      }
    },
    created() {
      this.$store.state.Apps.$on(this.refresh_page_method, () => {
        this.search();
      });
    },
    async mounted() {
      this.search();
    },
    methods: {
      handleSizeChange(val) //修改单页显示条数
      {
        this.searchFormData.pageSize = val;
        this.search();
      },
      handleCurrentChange(val) //当前页修改
      {
        this.searchFormData.pageNum = val;
        this.search();
      },
      onReset() // 重置功能
      {
        this.$refs.searchFormRef.resetFields();
        this.search();
      },
      onSearch() // 查询按钮
      {
        this.searchFormData.pageNum = 1;
        this.search();
      },
      async search() {
        let data = await this.$search(this.searchFormData, 'OAMP_search_sys_update_info_list');
        this.searchFormData.pageCount = data.pageCount;
        this.tableData = data.data
        for (var i = 0; i < this.tableData.length; i++) {
          let contentString = ""
          for (var j = 0; j < this.tableData[String(i)].version_content.length; j++) {
            contentString += String(j + 1) + ". " + this.tableData[String(i)].version_content[j] + "   "
          }
          this.tableData[String(i)].version_content_format = contentString.replace(/(\s*$)/g, "")
        }
      },
      async onDelete(index, rows)  // 删除
      {
        let rowData = rows[index];
        if (this.$del(rowData, 'OAMP_delete_sys_update_info')) {
          rows.splice(index, 1);
          this.$message.success('删除数据成功');
          this.search();
        }
        else {
          this.$message.error('删除数据失败');
        }
      },
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



